<template>
  <div class="game-board">
    <table>
      <thead>
        <tr>
          <th v-for="(column, colIndex) in distributedColumns" :key="colIndex">
            {{ column.category }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="rowIndex in 5" :key="rowIndex">
          <td v-for="(column, colIndex) in distributedColumns" :key="colIndex">
            <v-btn
              v-if="getQuestionForCell(column, rowIndex)"
              :class="{ 'answered': isAnswered(column.category, getQuestionIndex(column, rowIndex)) }"
              :style="isAnswered(column.category, getQuestionIndex(column, rowIndex)) 
                      ? 'background-color: grey; color: white;' 
                      : 'background-color: lightblue; color: purple;'"
              @click="$emit('ask-question', { category: column.category, index: getQuestionIndex(column, rowIndex) })"
            >
              {{ getQuestionForCell(column, rowIndex).points }} POINTS
            </v-btn>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  props: {
    categories: Array, // List of selected categories
    displayedQuestions: Object, // Questions by category
    openedQuestions: Array, // List of opened questions
    answeredQuestions: Array, // List of answered questions
  },
  computed: {
    distributedColumns() {
      const columns = [];
      this.categories.forEach((category) => {
        const categoryQuestions = this.displayedQuestions[category] || [];
        for (let i = 0; i < Math.ceil(categoryQuestions.length / 5); i++) {
          columns.push({
            category: category,
            startIndex: i * 5,
          });
        }
      });
      return columns;
    },
  },
  methods: {
    getQuestionForCell(column, rowIndex) {
      const category = column.category;
      const questionIndex = column.startIndex + (rowIndex - 1);
      if (this.displayedQuestions[category] && this.displayedQuestions[category][questionIndex]) {
        return this.displayedQuestions[category][questionIndex];
      }
      return null;
    },
    getQuestionIndex(column, rowIndex) {
      return column.startIndex + (rowIndex - 1);
    },
    isAnswered(category, index) {
      return this.answeredQuestions.some(q => q.category === category && q.index === index);
    },
  },
};
</script>

<style scoped>
.game-board {
  width: 100%;
  max-width: 800px;
  margin: auto;
  border-collapse: collapse;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: center;
  padding: 5px;
}

v-btn {
  width: 100px;
  height: 40px;
  margin: 5px 0;
}

.greyed-out {
  background-color: lightgrey !important;
  pointer-events: none;
}

/* Add a total questions display */
.total-questions {
  margin-top: 20px;
  text-align: center;
}

.total-questions p {
  font-size: 16px;
  margin: 5px 0;
}

.answered {
  pointer-events: auto; /* Allows clicking on answered questions */
  cursor: pointer;
}
</style>