<template>
  <v-app>
    <v-main>
      <!-- Centered Title -->
      <v-row class="d-flex justify-center">
        <v-col cols="12" class="text-center">
          <h1 class="quiz-title">Telugu Jeopardy Game</h1>
        </v-col>
      </v-row>

      <v-container>
        <v-row class="align-start">
          <!-- Category Selection Dropdown -->
          <v-col cols="12" md="6">
            <v-select
              v-model="selectedCategories"
              :items="categories"
              label="Select Categories"
              multiple
              :disabled="gameStarted"
              style="background-color: orange; height: 55px; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;" 
            ></v-select>
            <p style="color: blue; margin-top: 15px;">
              (Please select one to five categories and then click "Start Game.")
            </p>
          </v-col>

          <!-- Start and Reset Game Buttons -->
          <v-col cols="auto">
            <v-btn 
              class="start-game-btn" 
              :disabled="gameStarted" 
              @click="handleStartGame" 
              style="width: 100px; height: 50px;" 
              color="green">
              START
            </v-btn>
            <v-btn 
              @click="resetGame" 
              style="width: 100px; height: 50px; margin-left: 10px;" 
              color="blue darken-4">
              RESET
            </v-btn>
          </v-col>
        </v-row>

        <!-- Error message if any -->
        <p v-if="errorMessage" style="color: red; font-size: 30px;" class="blinking-text">
          {{ errorMessage }}
        </p>

        <!-- Centered GameBoard with Left Shift -->
        <v-row class="d-flex justify-start mt-4">
          <v-col cols="12" md="8" class="text-center">
            <GameBoard
              v-if="gameStarted"
              :categories="selectedCategories"
              :openedQuestions="openedQuestions"
              :answeredQuestions="answeredQuestions"
              :displayedQuestions="displayedQuestions"
              @ask-question="handleQuestion"
            />
          </v-col>
        </v-row>

        <!-- Player Scores Section -->
        <v-row class="d-flex justify-start mt-4">
          <v-col cols="12" md="8" class="text-center">
            <div v-if="gameStarted" class="player-scores">
              <h3>Player Scores</h3>
              <table class="score-table">
                <tr>
                  <th :class="currentPlayer === 0 ? 'active-player' : 'inactive-player'">Player 1</th>
                  <th :class="currentPlayer === 1 ? 'active-player' : 'inactive-player'">Player 2</th>
                </tr>
                <tr>
                  <td>{{ scores[0] }}</td>
                  <td>{{ scores[1] }}</td>
                </tr>
              </table>
            </div>
          </v-col>
        </v-row>

        <!-- Question Modal -->
        <v-dialog v-model="showModal" persistent max-width="600px" style="top: -50px;">
          <v-card>
            <v-card-title style="white-space: normal; word-wrap: break-word;">
              {{ currentQuestion.question }}
            </v-card-title>

            <v-card-text>
              <p>Points: {{ currentQuestion.points }}</p>
              <p v-if="showAnswer || isAnswered(currentQuestion.category, currentQuestion.index)" 
                 style="white-space: normal; word-wrap: break-word;">
                Answer: {{ currentQuestion.answer }}
              </p>
            </v-card-text>

            <v-card-actions>
              <template v-if="!isAnswered(currentQuestion.category, currentQuestion.index)">
                <template v-if="!showAnswer">
                  <v-btn color="blue darken-1" text @click="revealAnswer">Show Answer</v-btn>
                  <v-btn color="orange darken-1" text @click="skipQuestion">Skip</v-btn>
                </template>
                <template v-else>
                  <v-btn color="green darken-1" text @click="submitAnswer(true)">Correct</v-btn>
                  <v-btn color="red darken-1" text @click="submitAnswer(false)">Incorrect</v-btn>
                  <v-btn color="orange darken-1" text @click="skipQuestion">Skip</v-btn>
                </template>
              </template>
              <template v-else>
                <v-btn color="blue darken-1" text @click="closeModal">Close</v-btn>
              </template>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- Winner Announcement Modal -->
        <v-dialog v-model="showWinnerModal" persistent max-width="600px">
          <v-card>
            <v-card-title 
              style="font-weight: bold; font-size: 24px; text-align: center; margin-bottom: 10px;">
              Game Over
            </v-card-title>

            <v-card-text style="text-align: center; font-size: 18px;">
              <p style="font-weight: bold; color: darkgreen; margin-bottom: 15px;">
                {{ winnerMessage }}
              </p>
              <p style="font-weight: bold; margin-bottom: 10px;">Final Scores:</p>
              <ul style="list-style: none; padding: 0; font-size: 18px;">
                <li style="margin-bottom: 10px;">
                  üèÜ Player 1: <strong>{{ scores[0] }}</strong>
                </li>
                <li>üèÜ Player 2: <strong>{{ scores[1] }}</strong></li>
              </ul>
            </v-card-text>

            <v-card-actions style="justify-content: center;">
              <v-btn 
                color="blue darken-1" 
                text 
                @click="showWinnerModal = false" 
                style="font-size: 18px; padding: 12px 24px;">
                Close
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Papa from 'papaparse';
import GameBoard from './components/GameBoard.vue';

export default {
  name: 'App',
  components: {
    GameBoard,
  },
  data() {
    return {
      categories: [],
      selectedCategories: [],
      questions: {},
      showModal: false,
      showWinnerModal: false,  
      currentQuestion: {},  
      scores: [0, 0],
      currentPlayer: 0,
      showAnswer: false,
      openedQuestions: [],
      answeredQuestions: [], 
      gameStarted: false,
      totalSelectedQuestions: 0,  
      displayedQuestions: {},  
      questionsAnswered: 0,  
      totalQuestions: 25,  
      winnerMessage: '',  
      errorMessage: '',
      winnerAnnounced: false
    };
  },
  methods: {
    loadQuestions() {
      const csvFile = process.env.NODE_ENV === 'production' ? '/jeopardy-game/questions.csv' : '/questions.csv';
      Papa.parse(csvFile, {
        header: true,
        download: true,
        complete: (result) => {
          this.processQuestions(result.data);
        }
      });
    },
    processQuestions(data) {
      const categories = new Set();
      const questions = {};
      data.forEach((row) => {
        const { Category, Question, Answer, Marks } = row;
        if (Category && Question && Answer && Marks) {
          categories.add(Category);
          if (!questions[Category]) questions[Category] = [];
          questions[Category].push({
            question: Question,
            answer: Answer,
            points: parseInt(Marks, 10),
          });
        }
      });
      this.categories = Array.from(categories);
      this.questions = questions;
    },
    shuffle(array) {
      let currentIndex = array.length, randomIndex;
      while (currentIndex !== 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
      }
      return array;
    },
    handleStartGame() {
      if (this.selectedCategories.length === 0) {
        this.errorMessage = "Please select at least one category.";
        return;
      }
      if (this.selectedCategories.length > 5) {
        this.errorMessage = "Please select only a maximum of 5 categories.";
        return;
      }

      this.errorMessage = "";
      this.gameStarted = true;

      let maxQuestionsPerCategory = 25;
      const numSelectedCategories = this.selectedCategories.length;

      if (numSelectedCategories === 2) {
        maxQuestionsPerCategory = 20;
      } else if (numSelectedCategories === 3) {
        maxQuestionsPerCategory = 10;
      } else if (numSelectedCategories >= 4) {
        maxQuestionsPerCategory = 5;
      }

      const sortedCategories = [...this.selectedCategories].sort((a, b) => {
        const availableQuestionsA = this.questions[a]?.length || 0;
        const availableQuestionsB = this.questions[b]?.length || 0;
        return availableQuestionsA - availableQuestionsB;
      });

      const selectedQuestions = {};
      let totalSelectedQuestions = 0;
      let remainingQuestions = this.totalQuestions;
      let remainingSelectedCategories = sortedCategories.length;
      let questionsPerCategory = Math.max(Math.floor(remainingQuestions / (remainingSelectedCategories * 5)) * 5, 5);

      sortedCategories.forEach((category) => {
        const shuffledQuestions = this.shuffle([...this.questions[category]]);
        const questionCount = Math.max(Math.floor(shuffledQuestions.length / 5) * 5, 5);
        selectedQuestions[category] = shuffledQuestions.slice(0, Math.min(questionCount, questionsPerCategory, maxQuestionsPerCategory));
        selectedQuestions[category].sort((a, b) => a.points - b.points);
        totalSelectedQuestions += selectedQuestions[category].length;
        remainingQuestions -= selectedQuestions[category].length;
        remainingSelectedCategories--;
        questionsPerCategory = Math.floor(remainingQuestions / remainingSelectedCategories);
      });

      this.totalSelectedQuestions = totalSelectedQuestions;
      this.displayedQuestions = selectedQuestions;
      this.questionsAnswered = 0;
    },
    handleQuestion({ category, index }) {
      const selectedQuestion = this.displayedQuestions[category][index];
      if (selectedQuestion) {
        this.currentQuestion = { ...selectedQuestion, category, index };
        this.showModal = true;
        this.showAnswer = false;
      }
    },
    revealAnswer() {
      this.showAnswer = true;
    },
    submitAnswer(isCorrect) {
      if (isCorrect) {
        this.scores[this.currentPlayer] += this.currentQuestion.points;
      } else {
        this.scores[this.currentPlayer] -= this.currentQuestion.points;
      }
      this.markAnswered(this.currentQuestion.category, this.currentQuestion.index);
      this.closeModal();
      this.checkGameOver();
      this.switchPlayer();
    },
    skipQuestion() {
      this.markAnswered(this.currentQuestion.category, this.currentQuestion.index);
      this.closeModal();
      this.checkGameOver();
      this.switchPlayer();
    },
    closeModal() {
      this.showModal = false;
      this.showAnswer = false;
    },
    switchPlayer() {
      this.currentPlayer = (this.currentPlayer + 1) % this.scores.length;
    },
    checkGameOver() {
      const totalQuestions = Object.values(this.displayedQuestions).flat().length;
      //console.log(`Questions Answered: ${this.questionsAnswered}, Total Questions: ${totalQuestions}`);
      if (this.questionsAnswered >= totalQuestions && !this.winnerAnnounced) {
        this.winnerAnnounced = true;
        this.showWinner();
      }
    },
    showWinner() {
      //console.log(`Scores - Player 1: ${this.scores[0]}, Player 2: ${this.scores[1]}`);
      if (this.scores[0] > this.scores[1]) {
        this.winnerMessage = 'Player 1 is the winner!';
      } else if (this.scores[1] > this.scores[0]) {
        this.winnerMessage = 'Player 2 is the winner!';
      } else {
        this.winnerMessage = "It's a tie!";
      }
      this.showWinnerModal = true;
    },
    resetGame() {
      this.scores = [0, 0];
      this.openedQuestions = [];
      this.answeredQuestions = [];
      this.questionsAnswered = 0;
      this.gameStarted = false;
      this.showWinnerModal = false;
      this.selectedCategories = [];
      this.displayedQuestions = {};
      this.errorMessage = "";
      this.winnerAnnounced = false;
    },
    markAnswered(category, index) {
      if (!this.isAnswered(category, index)) {
        this.answeredQuestions.push({ category, index });
        this.questionsAnswered++;
      }
    },
    isAnswered(category, index) {
      return this.answeredQuestions.some(q => q.category === category && q.index === index);
    },
    handleEscapeKey(event) {
      if (event.key === "Escape" && this.showModal) {
        this.skipQuestion();
      }
    }
  },
  mounted() {
    this.loadQuestions();
    window.addEventListener("keydown", this.handleEscapeKey);
  },
  beforeUnmount() {
    window.removeEventListener("keydown", this.handleEscapeKey);
  }
};
</script>

